<template>
  <div class="container">
    <div class="p1">
      <div class="title">抗击新冠肺炎</div>
      <div class="title-1">为了疫情可控，请大家主动登记</div>
    </div>
    <div class="p2">
      <el-form :label-position="labelPosition" ref="form" :model="form" label-width="60px">
      <el-form-item label="人员名称">
        <el-input v-model="form.username" placeholder="必填"></el-input>
      </el-form-item>
      <el-form-item label="联系电话">
        <el-input v-model="form.phone" placeholder="必填"></el-input>
      </el-form-item>
      <el-form-item label="身份证号码">
        <el-input v-model="form.cardnumber" placeholder="必填"></el-input>
      </el-form-item>
      <el-form-item label="居住地址">
        <el-input v-model="form.address" placeholder="必填"></el-input>
      </el-form-item>
      <el-form-item label="测量体温">
        <el-input v-model="form.temperature" placeholder="必填"></el-input>
      </el-form-item>
    </el-form>
    </div>
    <div class="p3">
      <el-button type="primary" @click="onSubmit">登记</el-button>
    </div>
  </div>
</template>
<style scoped>
.container {
  width: 100%;
  height: 100%;
  /* background-color: red; */
   background-color: #009ad6;
}
.container .p1{
  width: 90%;
  margin: 0 auto;
  padding: 10px;
  color: #fff;
}
.container .p1 .title{
  font-size: larger;
}
.container .p1 .title-1{
  font-size: small;
  margin-top: 10px;
  background-color: rosybrown;
  width: fit-content;
  /* border-radius: 10px; */
  padding: 0px 4px;
}
.container .p2{
  padding: 10px 10px;
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px 5px 0px 0px;
}
.container .p3{
  padding: 10px 10px;
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  text-align: center;
  border-radius: 0px 0px 5px 5px;
}
</style>
<script>
export default {
  name: "infomationadd",
  methods: {
    onSubmit: function() {
      // alert(this.form);
      this.$axios({
               method:'post',
               url:'http://localhost:8080/info/insert',
               data:this.form,
               //withCredentials:true, //局部携带cookie
               headers:{} //如果需要添加请求头可在这写
           }).then(res => {  //res是返回结果
                console.log(res.data.code)
                if(res.data.code===200){
                  this.$router.push({name: 'success'})
                }
                else{
                  this.$router.push({name: 'error'})
                }
                // console.log(res.data)
           }).catch(err => { //请求失败就会捕获报错信息
                 //err.response可拿到服务器返回的报错数据       
           })
    }
  },
  data() {
    return {
      labelPosition:'top',
      form: {
        name: "",
        phone: "",
        cardnumber: "",
        address: "",
        temperature: ""
      }
    };
  }
};
</script>


